@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
@import url(https://cdn.jsdelivr.net/npm/fork-awesome@1.1.7/css/fork-awesome.min.css);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
*{
    box-sizing: border-box;
}
html {
    overflow-x: hidden;
}
body {
    font-family: 'Nunito Sans', sans-serif;
    width: 100%;
}
#grid{
    display: grid;
    grid-template-areas:
        "header header header header header"
        "main main main main main"
        "footer footer footer footer footer";
    grid-template-rows: 50px 1fr 200px;
    grid-template-columns: 1fr repeat(3, minmax(0,400px)) 1fr;
}
#header {
    grid-area: header;
    background-color: white;
    z-index: 5;
    position: fixed;
    width: 100%;
}
#main {
    grid-area: main;
}
#home-page{
    display: grid;
    grid-template-areas:
    "carousel carousel carousel carousel carousel" 
    ". posts-area posts-area sidebar .";
    grid-template-columns: 1fr repeat(3, minmax(0,400px)) 1fr;
    grid-template-rows: 60vh auto;
}
#category-page {
    display: grid;
    grid-template-areas:
        "banner banner banner banner banner"
        ". posts-area posts-area sidebar .";
    grid-template-rows: 55vh 1fr;
    grid-template-columns: 1fr repeat(3, minmax(0,400px)) 1fr;
    width: 100%;
}
#banner{
    grid-area: banner;
}
#carousel{
    grid-area: carousel;
}
#sidebar {
    grid-area: sidebar;
    padding: 30px 0 0 0;
    background-color: #F8F8F8;
}
#posts-area{
    grid-area: posts-area;  
    padding-right: 15px;
    background-color: #F8F8F8;
}
#footer {
    grid-area: footer;
    display: grid;
    grid-template-areas: 
    "newsletter social contacts";
    grid-template-columns: repeat(3, 33.3%);
    background-color: #404250;
    padding: 30px;
}
#logo{
    grid-area: logo;
}
#newsletter{
    grid-area: newsletter;
}
#social{
    grid-area: social;
}
#contacts{
    grid-area: contacts;  
}
.container {
    width: 1201px;
    margin: 0 auto;
}
.category{
    width: 90px;
    height: 35px;
    bottom: 25%;
    color: white;
    outline: none;
    border: 0;
    font-weight: 700;
    font-size: 18px;
    border-radius: 3px;
    background-color: #FF4040;
    z-index: 2;
}
@media (max-width: 1200px){
    .container{
        width: 80%;
        position: relative;
    }
    #main .full-post{
        grid-template-areas: 
        "post";
        grid-template-columns: 100%;
    }
}

@media (min-width: 1500px){
    #grid {
        grid-template-columns:  1fr repeat(3, 500px) 1fr;
    }
    .container{
        width: 1500px;
        position: relative;
    }
    .popular-post{
        grid-template-rows: 150px;
    }
    #carousel .category-title .slider-title{
        font-size: 2rem;
        line-height: 50px;
    }
    #carousel .category-title .slide-category{
        width: 120px;
        height: 40px;
    }
    #carousel .category-title .category{
        font-size: 22px;
    }
} 

@media (max-width: 991px) {
    #grid {
        grid-template-areas:
            "header header"
            "main main"
            "footer footer";
        grid-template-rows: 60px auto auto;
        grid-template-columns: 60% 40%; 
        height: 100%;
        width: 100%;
        }
    .container{
        width: 100%;
        position: relative;
    }   
    #header nav{
        height: 60px;
    } 
    nav .burger-menu {
        display: inline-block;
        position: absolute;
        right: 0;
        cursor: pointer;
        color: #404250;
        position: absolute;
        top: 50%;                         
        -webkit-transform: translate(0, -50%);                         
                transform: translate(0, -50%);
        right: 30px;
    }
    nav .logo{
        margin-left: 15px;
    }
    nav .main-menu {
        display: none;
    }
    nav .link-effect::after{
        display: none; 
    }
    nav .main-menu a:active,
    nav .main-menu a:focus{
        background-color: rgb(211, 211, 211);
    }
    nav #nav-toggle{
        display: none;
    }
    #nav-toggle:checked ~.main-menu {
        display: grid;
        z-index: 5;
        grid-template-rows: repeat(6, 1fr);
        grid-template-columns: 100%;
        width: 50%;
        position: absolute;
        top: 100%;
        right: 0;
    }
    nav .main-menu a{
        padding: 15px;
        background-color: #F8F8F8;
    }
    .slider-item .category-title{
        top: 55%;
    }
    .popular-post{
        grid-template-rows: 100px;
    }
    #footer {
        grid-template-areas:
        "social newsletter contacts";
        grid-template-rows: 1fr;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 100px;
        -webkit-column-gap: 100px;
           -moz-column-gap: 100px;
                column-gap: 100px;
    }
    .newsletter-wrapper, .social-wrapper, .contacts-wrapper{
        justify-content: start;
    }
    .newsletter-wrapper input, button{
        margin-bottom: 15px;
    }
    #footer>.logo{
        margin-left: 0;
    }
    #social{
        justify-content: start;
    }
} 
@media (max-width: 768px) { 
    #grid {
    grid-template-areas:
        "header"
        "main"
        "footer";
    grid-template-rows: 60px auto auto;
    grid-template-columns: 100%; 
    height: 100%;
    }
    nav{
    grid-template-columns: auto 1fr;
    grid-template-rows: 60px;
    }
    #nav-toggle:checked ~.main-menu{
        width: 100%;
    }
    #carousel .category-title  .slider-title{
        margin: 0 15px 15px 15px;
    }
    .category-title>.slide-category{
        margin-left: 25px;
    }
    .container{
    width: 100%;
    position: relative;
    }
    #home-page{
        grid-template-areas:
        "carousel carousel" 
        "posts-area  posts-area"
        "sidebar sidebar";
        grid-template-columns: 1fr;
        grid-template-rows: 60vh auto;
    }
    #home-page #posts-area{
        padding-right: 0;
    }
    .post-item .post-footer{
        padding: 15px;
    }
    #sidebar{
        padding-top: 0;
    }
    #sidebar .popular-post{
        margin: 15px;
        grid-template-columns: 30% 70%;
        grid-template-rows: 120px;
    }
    .popular-posts-header{
        margin-left: 10px;
        margin-right: 10px;
    }
    #sidebar .popular-post-body{
        padding-right: 0;
    }
    .popular-post-body>h5{
        font-size: 1.3rem;
    }
    .popular-post-body>.appearing-link{
        display: none;
    }
    .popular-post .state-link{
        display: block;
        text-decoration: none;
        color: #FF4040;
        text-align: end; 
        align-self: end;
    }
    .popular-post .state-link>i{
        margin-left: 5px;
    }
    .random-post-wrap{
        padding: 0 15px;
    }
    .newsletter-wrapper{
        grid-row-gap: 15px;
        row-gap: 15px;
    }
    .newsletter-wrapper h2{
        text-align: center;
    }
    #footer {
        grid-template-areas:
        "newsletter"
        "social"
        "contacts";
        grid-template-rows: repeat(3, auto);
        grid-template-columns: 1fr;
        justify-items: center;
        grid-row-gap: 30px;
        row-gap: 30px;
    }
    #footer .logo{
        margin-left: 0;
    }
    .contacts-wrapper h2{
        text-align: center;
    }
} 



