} 


.nav-item{
    display: grid;
    align-items: center;
    text-align: center;
}
.main-menu {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
}
#nav-search:checked~.search{
    display: inline-block;
    position: absolute;
}
.main-menu>a {
    height: 100%;
    color: #404250;
